.card {
  padding: 32px 16px 16px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
}

.cards__card {
  width: $cardWidth;
  margin: 0 $cardGap 46px 0;

  &:nth-child(4n) {
    margin-right: 0;
  }

  &:nth-last-child(-n + 4) {
    margin-bottom: 0;
  }
}

.card__img {
  max-width: 100%;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  margin-bottom: 4px;
  font-size: 12px;
  color: $text-accent-color;
  line-height: 18px;
  font-weight: 500;
}

.card__code {
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__feedback {
  margin-bottom: 24px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.card__reviews {
  color: $text-accent-color;
}

.card__price {
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 18px;
}

.card__value {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: $text-accent-color;
}

.card__btn {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-decoration: none;
  background: $main-color;
  border: 1px solid transparent;
  border-radius: 5px;
}

.card__btn:hover {
  color: $main-color;
  background: #fff;
  border: 1px solid $main-color;
}
